import React from 'react'
const routers = [
  {
    path: '/a',
    component: () => <div>a - hah - a</div>
  },
  {
    path: '/b',
    component: () => <div>b - hah - b</div>
  },
  {
    path: '/c',
    component: () => <div>c - hah - c</div>
  }
]

module.exports = routers



